$navy: #143151; // --primary-color
$cyan: #00b5b5;    // --solais-cyan
@use "bulma/sass/utilities"  as b-util with (
  $primary: $navy,
  $link: $cyan,
);

// Import all Bulma components
@use "bulma/sass/base";
//@use "bulma/sass/themes";

//@use "bulma/sass/utilities/css-variables" as cv;

@use "bulma/sass/themes";

@use "bulma/sass/elements" with (
    $content-heading-weight: b-util.$weight-semibold,
    $title-weight: b-util.$weight-semibold,
    $title-color: hsl(211, 60%, 20%),
    $content-blockquote-border-left: 5px solid $cyan,
    $content-blockquote-background-color: var(--bulma-primary-soft),
);
//@use "bulma/sass/form";
@use "bulma/sass/components" with (
    $navbar-burger-color: $cyan,
    $navbar-dropdown-background-color: $navy,
);
@use "bulma/sass/grid";
@use "bulma/sass/helpers";
@use "bulma/sass/layout";

@use "services" as svc; // loads all variables, mixins, and functions from services.scss
@use "main";
@use "syntax"; // Rouge syntax highlighting

:root {
    // --primary-color: #143151;
    // --solais-cyan: #00b5b5;
    // --primary-color-alpha-25: rgba(255, 255, 255, 0.25);
    // --text-color-white: white;
    // --text-color-grey: grey;
     --black: #000000;
    //--black-alpha-90: rgba(0, 0, 0, 0.9);
    --shadow-light: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
    --shadow-medium: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
    --border-radius: 0.25rem;
    --transition-fast: 0.15s ease-in-out;
    --transition-medium: 0.25s ease;
    --spacing-small: 0.5rem;
    --spacing-medium: 0.75rem;
    --spacing-large: 1.5rem;
    --spacing-xl: 2rem;
    --navbar-height: 4rem;
    //--bulma-navbar-dropdown-background-color: $navy !important;
}

.section {
    //@extend .has-text-left;
    @extend .has-text-black-ter;

    padding-top: var(--spacing-large);
    padding-bottom: var(--spacing-small);
    //margin: 0px 0px;
    overflow: hidden;

    @media (max-width: 768px) {
        padding-left: 0.25rem;
        padding-right: 0.25rem;
    }
}

// Global .content styling handled by Bulma configuration above
// Post-specific .content styling in #post section below

.column-container {
    padding-bottom: 0.75rem;
}

// * {
//   outline: 1px solid red;
// }

/* Offset anchor targets to account for fixed navbar height */
[id] {
    scroll-margin-top: var(--bulma-navbar-height);
}

.page-btn {
    @extend .button;
    @extend .is-uppercase;
    //style
    @extend .is-primary;
    @extend .is-outlined;
    @extend .is-rounded;
    //size
    @extend .is-medium;
    @extend .is-responsive;
}

.page-btn:hover {
  background-color: var(--bulma-primary);
  border-color: var(--bulma-link);
  color: var(--bulma-link);
}

.footer-btn {
    @extend .button;
    @extend .is-rounded;
    @extend .is-uppercase;
    @extend .is-outlined;
    @extend .is-link; 

    margin: 0.5rem auto;

    transition: var(--transition-fast);
    /* smooth hover/active changes */
}



// dropdown arrow colour
.navbar-link:not(.is-arrowless)::after {
    border-color: var(--solais-cyan);
}

.navbar.is-primary {
    --bulma-navbar-item-color-l: 99%;
}
