#hero {
    position: relative;
    height: 100dvh;
    min-height: 100dvh;
    margin-top: var(--bulma-navbar-height);
    background: rgb(152, 151, 151);
    background: -moz-linear-gradient(0deg,
            var(--black) 0%,
            rgba(0, 0, 0, 0.75) 100%);
    background: -webkit-linear-gradient(0deg,
            var(--black) 0%,
            rgba(0, 0, 0, 0.75) 100%);
    background: linear-gradient(0deg,
            var(--black) 0%,
            rgba(0, 0, 0, 0.75) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000",
            endColorstr="#000000",
            GradientType=1);

    &::before {
        filter: blur(0.15rem);
        background-image: url(https://solaissecurity.com/assets/images/hero-wide.png);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-attachment: fixed;

        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        position: absolute;
        opacity: 0.25;
        content: "";

        @media (max-width: 768px) {
            background-image: url(https://solaissecurity.com/assets/images/hero-tall.png);
            background-size: cover;
            background-attachment: scroll;
        }
    }
}



