/* List styling for lists OUTSIDE .content (e.g., about page) */
/* Lists inside .content use Bulma's native styling */
:not(.content) > ul,
:not(.content) > ol {
    margin: var(--spacing-large) 0;
    padding-left: 1.5em;
}

:not(.content) > ul {
    list-style-type: disc;
}

:not(.content) > ol {
    list-style-type: decimal;
}

/* Square bullet lists */
ul.square {
    list-style-type: square;
}

/* List item spacing for non-.content lists */
:not(.content) > ul > li,
:not(.content) > ol > li {
    margin: var(--spacing-small) 0;
    padding-left: 0.25em;
    text-indent: 0;
    line-height: 1.5;
}

/* Optional: "inside" style */
ul.inside,
ol.inside {
    list-style-position: inside;
    padding-left: 0;
}

#about {
    .image {
        max-width: 20vmax;
        margin: auto;

        img {
            border: 0.25rem dotted var(--black);
        }
    }

    a {
        min-width: 25vmax;
        margin: 0.5rem;
    }
}


#contact-button-1 {
    display: flex;
    justify-content: center;
}

#contact {
    svg {
        transition: var(--transition-fast);
        margin: auto var(--spacing-medium);

        &:hover {
            transform: scale(1.1);
            filter: opacity(0.75);
        }
    }

    hr {
        background-color: var(--black);
    }
}

#blog {
    .hero-body {
        padding: 3rem 0.25rem 1rem 0.25rem;
    }

    .container {
        margin: 1.5rem auto;
        padding-left: 1rem;
        padding-right: 1rem;
        max-width: 1200px;
    }

    #blog-card {
        transition: all 0.25s ease;
        margin-bottom: 0.5vmax;
        transform: scale(0.85);
        margin-right: 2.5vmax;
        margin-left: 2.5vmax;
        line-height: normal;
        border-radius: 0.25rem;
        box-shadow: var(--shadow-light);

        &:hover {
            transform: scale(0.875);
            box-shadow: var(--shadow-medium);
        }

        @media (max-width: 768px) {
            transform: scale(1);
            margin-bottom: 5vmax !important;

            &:hover {
                transform: scale(1.025);
                margin-bottom: 5vmax !important;
                box-shadow: var(--shadow-medium);
            }

            .image {
                padding-top: 50% !important;
            }
        }

        .title {
            margin: 0.5rem !important;
        }

        .content {
            margin: 0.5rem;
        }

        .column {
            display: grid;
            // grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
            // gap: var(--spacing-medium);

            .image {
                border-radius: var(--border-radius);
                background-position: center;
                background-repeat: no-repeat;
                background-size: cover;
            }
        }
    }
}



#search {
    border-bottom: 0.15rem solid;

    input {
        border: none;
        outline: none;
        font-weight: 500 !important;

        &::placeholder {
            font-weight: 500;
            color: var(--text-color-grey);
            letter-spacing: 0.25rem;
        }
    }

    .button {
        transition: var(--transition-medium);

        &:focus {
            outline: 0.25rem solid var(--text-color-white);
        }

        border: none;
    }
}

footer {
    #backtotop {
        margin-bottom: 1rem; //fix overlap with grey background footer

        &:hover {
            transform: scale(1.05);
        }
    }

    .column {

        li {
            list-style: circle;
            //list-style-position: outside;
        }

        figure {
            border: 1px solid var(--text-color-white);
            border-radius: 50rem;
            //justify-content: center;
            //margin: 0 auto;
            margin-left: auto;
            margin-right: auto;

            transition: var(--transition-medium);

            &:hover {
                transform: scale(1.15);
            }
        }

    }
}

