#post {
    @media (min-width: 768px) {
        margin: auto 2.5vmax;
    }

    a {
        text-decoration: underline;
    }

    #title {
        text-decoration: none;
    }

    .title {
        margin: auto;
    }

    #post-image {
        border-radius: var(--border-radius);
    }

    .subtitle {
        letter-spacing: 0.05rem;
    }

    #description {
        margin-bottom: 1.25vmax;
    }

    .tag {
        margin: 0.15rem auto;
    }

    .content {
        /* Bulma's .content handles ul/ol styling natively - no overrides needed */
        blockquote {
            max-width: 100%;
        }

        hr {
            background-color: var(--text-color-grey);
        }

        img,
        iframe {
            max-width: calc(100% - 25vw);
            margin: 0.25rem auto;

            @media (max-width: 768px) {
                max-width: 100%;
            }
        }

        // Bulma already provides: white-space: pre, overflow-x: auto for pre blocks
        // Override only for larger screens where we want wrapping instead
        pre {
            @media (min-width: 768px) {
                white-space: pre-wrap;
                word-wrap: break-word;
                overflow-wrap: break-word;
            }
        }

        // Scrollable tables on mobile - mirrors Bulma's .table-container pattern
        // Markdown tables render without wrapper, so apply directly
        table {
            @media (max-width: 768px) {
                display: block;
                overflow-x: auto;
                -webkit-overflow-scrolling: touch;
                max-width: 100%;

                td, th {
                    max-width: 250px;
                    white-space: normal;
                    word-break: normal;
                    overflow-wrap: break-word;
                }
            }
        }
    }

    .container {
        margin: 2.5rem auto;
        word-break: break-word;
        max-width: 100%;
        padding: 0 1rem;
        box-sizing: border-box;

        @media (min-width: 769px) {
            max-width: 1200px;
        }
    }

    // #disqus_thread {
